import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileZipSrcListResponse } from "../../Model/Response/FileZipSrcListResponse";
import { FileZipSrcResponse } from "../../Model/Response/FileZipSrcResponse";
import { FileZipSrcViewModel } from '../../Model/ViewModel/FileZipSrcViewModel';
import { FileServicesProvider } from '../file-services/file-services';

/*
  Generated class for the FileZipSrcServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileZipSrcServicesProvider extends FileServicesProvider {
  private FileZipSrcs: FileZipSrcListResponse;
  private FileZipSrc: FileZipSrcResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "FileZipSrc";
    console.log("Hello FileZipSrcServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_group:string,exec_file_seq:number,order_type:boolean) {
    var sub_url: string;
    if(exec_file_seq===0)
    {
      sub_url = "FileZipSrc?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&order_type={5}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,order_type);
    }
    else
    {
      sub_url = "FileZipSrc/Select?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&exec_file_seq={5}&order_type={6}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,exec_file_seq,order_type);
    }
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileZipSrcs = new FileZipSrcListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileZipSrcs;
    });
  }



  public PostPutAsync(FileZipSrcViewModel:FileZipSrcViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "FileZipSrc";
    else
    sub_url = "FileZipSrc/{1}";
    sub_url = String.Format(sub_url, this.ctl,FileZipSrcViewModel.exec_file_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileZipSrcViewModel,mode).map(
      item => {
        this.FileZipSrc = new FileZipSrcResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileZipSrc;
      }
    );
  }
}



