import { SingleModelResponse } from "./SingleModelResponse";
import { FileZipSrcViewModel } from "../ViewModel/FileZipSrcViewModel";

export class FileZipSrcResponse extends SingleModelResponse<FileZipSrcViewModel> {
  Model: FileZipSrcViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new FileZipSrcViewModel();
      this.Model.exec_file_seq=obj.Model.exec_file_seq;
      this.Model.exec_group=obj.Model.exec_group;
      this.Model.file_keyword=obj.Model.file_keyword;
      this.Model.file_min_length=obj.Model.file_min_length;
      this.Model.file_max_length=obj.Model.file_max_length;
      this.Model.is_active=obj.Model.is_active;
      this.Model.recursive=obj.Model.recursive;
      this.Model.file_proc_key=obj.Model.file_proc_key;
      this.Model.exec_seq=obj.Model.exec_seq;
      this.Model.can_rerun=obj.Model.can_rerun;
      this.Model.parameter_column_group=obj.Model.parameter_column_group;
      this.Model.creator=obj.Model.creator;
      this.Model.create_time=obj.Model.create_time;
      this.Model.modifier=obj.Model.modifier;
      this.Model.last_update_time=obj.Model.last_update_time;
      this.Model.note=obj && obj.Model.note || null;
      this.Model.file_proc=obj.Model.file_proc;
      this.Model.is_active_desc=obj.Model.is_active_desc;
      switch (obj.Model.is_active) {
        case false: {
          this.Model.is_active_color = "danger";
          break;
        }

        default:
          this.Model.is_active_color = "active";
      }
      this.Model.is_fail_stop=obj.Model.is_fail_stop;
      this.Model.src_file_path_key=obj.Model.src_file_path_key;
      this.Model.bk_file_path_key=obj.Model.bk_file_path_key;
      this.Model.zip_file_path_key=obj.Model.zip_file_path_key;
      this.Model.src_file_path=obj.Model.src_file_path;
      this.Model.bk_file_path=obj.Model.bk_file_path;
      this.Model.zip_file_path=obj.Model.zip_file_path;
      this.Model.is_processed_sql=obj.Model.is_processed_sql;
      if( this.Model.is_processed_sql===true)
      {
        this.Model.processed_sql_color="danger";
      }
      else
       this.Model.processed_sql_color="light";
    }
  }
}



